function Reality() {
  return (
    <section id="reality" className="">
      <div className="container -wide">
        <div className="row justify-content-center">
          <div className="col-lg-6 wow fadeInDown">
            <div className="block-title">
              <div className="title-header text-uppercase">Thc trng</div>
              <h4 className="title-title">
                Tr trong giai on t 4 - 10 tui
              </h4>
            </div>
            <div className="reality-child-list">
              <div className="reality-child">
                <div className="reality-child__image">
                  <img
                    src={require("../../assets/images/icon-reality-1.png")}
                    alt="D chn, kh tp trung, kh hnh thnh thi quen rn luyn ti nh"
                  />
                </div>
                <div className="reality-child__text">
                  <span>D chn, kh tp trung, </span>kh hnh thnh thi quen
                  rn luyn <span>ti nh</span>
                </div>
              </div>
              <div className="reality-child">
                <div className="reality-child__image">
                  <img
                    src={require("../../assets/images/icon-reality-2.png")}
                    alt="Khng c h thng  hc tp, rn luyn v khm ph cng nhau"
                  />
                </div>
                <div className="reality-child__text">
                  <span>Khng c h thng</span>  hc tp, rn luyn v khm
                  ph cng nhau
                </div>
              </div>
              <div className="reality-child">
                <div className="reality-child__image">
                  <img
                    src={require("../../assets/images/icon-reality-3.png")}
                    alt="D chn, kh tp trung, kh hnh thnh thi quen rn luyn ti nh"
                  />
                </div>
                <div className="reality-child__text">
                  Khng c nn tng ton din, chuyn su
                  <span>pht trin t duy</span>
                </div>
              </div>
            </div>
          </div>
          <div
            className="col-lg-6 wow fadeInLeft animated"
            data-wow-delay="0.5s"
          >
            <div
              className="text-center mt-5 mt-lg-3 wow fadeInRight animated"
              data-wow-delay="0.25s"
            >
              <img
                src={require("../../assets/images/img-reality.png")}
                alt="Thc trng tr trong giai on t 4 - 10 tui"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Reality;
